.MainWrapper {
    width: 80%;
    min-height: 600px;
    margin-left: 20%;
    margin-top: 2%;
    
    display: flex;
    flex-wrap: wrap;
    gap: 30px;    


                                  //Forms
  .FormElement{
    width: 180px;
    height: 140px;


    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;

    border: 2px solid rgb(151, 150, 150);
    border-radius: 8px;
    background-color: transparent;

    cursor: pointer;
    transition: transform 0.2s ease, box-shadow 0.2s ease;    

    &:hover {
      transform: translateY(-2px);
      box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);    
    }

    .FormEmmiter{
      width: 60%;
      height: 80%;
      background-color: pink;
      
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 10px;

      gap: 8px;
      overflow: hidden;

      h3{
        width: 100%;
        
        height: auto;

        text-align: center;
        font-size: 14px;
        color: rgb(73, 73, 73);
      }
      
      p{
        width: 100%;
        height: auto;

        font-size: 8px;
        text-align: center;

        background-color: white;
        border-radius: 10px;
      }
    }

  }




                                  //Button
  .HomeElement {
    width: 180px;
    height: 140px;

    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;

    border: 2px solid rgb(151, 150, 150);
    border-radius: 8px;
    background-color: transparent;


    cursor: pointer;
    transition: transform 0.2s ease, box-shadow 0.2s ease;


    &::before {
      content: '';
      position: absolute;
      width: 6px;
      height: 80%; 
      background-color: silver;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%); 
      border-radius: 3px;
    }

    
    &::after {
      content: '';
      position: absolute;
      width: 6px;
      height: 80%; 
      background-color: silver;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(90deg); 
      transform-origin: center; 
      border-radius: 3px;
    }

    &:hover {
      transform: translateY(-2px);
      box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);

      &::before,
      &::after {
        background-color: #4f46e5;
      }
    }
  }
}
